import { Fragment } from 'react';
import { 
  BrowserRouter,
  Route,
  Routes,
} from 'react-router-dom'
import './App.css';
import { Login, Dashboard }from "./Components";

function App() {

  return(
    <Fragment>

      <BrowserRouter>
        <Routes>
          <Route path="/" element={<Login/>} />
          <Route path="Dashboard" element={<Dashboard/>} />
        </Routes>
      </BrowserRouter>
    </Fragment>
  )
}

export default App;
