import "./Dashboard.css"
import { useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import { auth, db, storage } from "../../Firebase/Config"
import Swal from "sweetalert2";
import { 
  addDoc, 
  collection, 
  deleteDoc,
  doc, 
  getDocs, 
  query,
  updateDoc, 
  where
} from "firebase/firestore"
import { 
  signOut
} from "firebase/auth";
import {
  deleteObject,
  getDownloadURL,
  ref, 
  uploadBytes,
} from "firebase/storage";

function Dashboard() {
  const userState = localStorage.getItem("userState");
  const navigate = useNavigate();

  const logout = () => {
    signOut(auth).then(
      localStorage.setItem("userState", "logout"),
      navigate("/")
    )
  };

  const [sectionState, setSectionState] = useState("show"); //Section: edit/new/show

  const [search, setSearch] = useState("");

  const [articles, setArticles] = useState([]);
  const [articleId, setArticleId] = useState("");

  const [displaySwitcher, setDisplaySwitcher] = useState("text");
  const candidateInputEdit = useRef(null);
  const candidateInputNew = useRef(null);

  const [tempImage, setTempImage] = useState(null);

  const [title, setTitle] = useState("");
  const [image, setImage] = useState("");
  const [position, setPosition] = useState("");
  const [text, setText] = useState("");
  const date = new Date()
  
  const articlesCollection = collection(db, "articles");
  const storageRef = ref(storage, `images/${articleId}`);

  

  // Create an article
  const createArticle = () => {

    if(title === ""){
      Swal.fire({
        icon: 'error',
        title: 'Oops...',
        text: 'Please insert a title',
        timer: 2000
      })
    }else{
      getDocs(query(articlesCollection, where("title", "==", title))).then(data => {
        if(data.size === 0){
          if(tempImage){
            addDoc(articlesCollection, {
              date: date.toISOString(), 
              title: title, 
              position: position,
              text: text
            }).then(docRef=>{
              const storageRef = ref(storage, `images/${docRef.id}`);
              uploadBytes(storageRef, tempImage).then(()=>{
                getDownloadURL(storageRef).then(url => {
                  updateDoc(doc(db, "articles", docRef.id), {
                    image: url,
                  }).then(()=>{
                    readArticles();
                    resetStates();
                    setSectionState("show")
                  }).catch(e => {
                    console.log(e);
                  })
                }).catch(e => {
                  console.log(e);
                })
              }).catch(e => {
                console.log(e);
              })
              
            }).catch(e => {
              console.log(e);
            })
          }else{
            addDoc(articlesCollection, {
              date: date.toISOString(), 
              title: title, 
              image: "",
              position: position,
              text: text
            }).then(()=>{
              readArticles();
              resetStates();
              setSectionState("show");
            }).catch(e => {
              console.log(e);
            })
          }
        }else{
          Swal.fire({
            icon: 'error',
            title: 'Oops...',
            text: 'Title already exists',
            timer: 2000
          })
        }
      }).catch(e => {
        console.log(e);
      })
    }
  
  };


  // Read articles
  const readArticles = () => {

    getDocs(articlesCollection).then( data => {
      setArticles(data.docs.map(doc=>({ ...doc.data(), id: doc.id})));
    }).catch(e => {
      console.log(e);
    })

  }


  // Update an article
  const updateArticle = () => {

    getDocs(query(articlesCollection, where("title", "==", title))).then(data => {
      if(data.size === 0){
        if(title === ""){
          Swal.fire({
            icon: 'error',
            title: 'Oops...',
            text: 'Please insert a title',
            timer: 2000
          })
        }else{  
          updateDoc(doc(db, "articles", articleId), {
            date: date.toISOString(), 
            title: title, 
            position: position,
            text: text
          }).then(()=>{
            readArticles();
            resetStates();
            setSectionState("show");
          }).catch(e => {      
            console.log(e);      
          })
        }
      }else{
        Swal.fire({
          icon: 'error',
          title: 'Oops...',
          text: 'Title already exists',
          timer: 2000
        })
      }

    })


    

  }


  // Delete an article
  const deleteArticle = () => {

    if(image !== ""){
      deleteObject(storageRef).catch(e => {
        console.log(e);
      })
    }
    deleteDoc(doc(db, "articles", articleId)).then(()=>{
      readArticles();
      resetStates();
      setSectionState("show");
    }).catch(e => {
      console.log(e);
    })

  }


  // Upload Image
  const uploadImage = () => { 
    
    if(image !== ""){
      deleteObject(storageRef).catch(e => {
        console.log(e);
      })
    }
    uploadBytes(storageRef, tempImage).then(()=>{
      getDownloadURL(storageRef).then(url => {
        updateDoc(doc(db, "articles", articleId), {
          image: url
        }).then(()=>{
          setImage(url); 
          setTempImage(null);
          readArticles();
        }).catch(e => {      
          console.log(e);      
        })
      }).catch(e => {
        console.log(e)
      })
    }).catch(e => {
      console.log(e);
    })    
      
  }


  // Delete Image
  const deleteImage = () => {

    deleteObject(storageRef).then(
      updateDoc(doc(db, "articles", articleId), {
        image: ""
      }).then(()=>{
        setImage("");
        readArticles()
      }).catch(e => {      
        console.log(e);      
      })
    ).catch(e => {
      console.log(e);
    })

  }


  // Aux functions
  const changeImage = e => {
    if(e.target.files[0]){
      setTempImage(e.target.files[0]);
    }
  }

  const resetStates = () =>{
    setArticleId("");
    setTitle("");
    setImage("");
    setTempImage(null);
    setPosition("");
    setText("");
  }
  
  useEffect(() => {
    readArticles();
  }, [])
  
  const editArticle = (article) => {
    setSectionState("edit");

    setArticleId(article.id);
    setTitle(article.title);
    setImage(article.image);
    setPosition(article.position);
    setText(article.text);
  }
  
  if(userState === "loggedIn"){
    return (
      <div className="DashboardContainer">
        <nav>
          <div>Welcome {localStorage.getItem("email")}</div>
          <button onClick={logout}>Log out</button>
        </nav>

        {(sectionState==="show") ? (

          <section>
            <div className="section-header">
              <button onClick={()=>{
                setSectionState("new")
              }}>Add</button>
              <input placeholder={"Search..."} onChange={e => { setSearch(e.target.value)}}/>
            </div>
            <div className={"articles"}>

              {articles.map(article => {
                return(
                  (article.title.includes(search) || article.text.includes(search))?(
                    <div key={article.title} className="article" onClick={() => {editArticle(article)}}>
                      <div>Edited: {String(article.date).substring(0,10)}</div>
                      <div>{article.title}</div>
                      <div>{article.text}</div>
                    </div>
                  ):(
                    <div></div>
                  )
                )
              })}

            </div>
          </section>

        ):((sectionState==="edit") ? ( 

          <section className="individual-article">
            <div className="header">
              <button onClick={()=>{
                resetStates();
                setSectionState("show");
              }}>X</button>
            </div>
            <div className="content">
              <div className="title">
                <div className="left">Title</div> 
                <textarea value={title} className="right" onChange={e => {setTitle(e.target.value)}} />
              </div>
              <div className="picture">
                <div className="left">    
                  <div className={image?"editor-picture-left-div1":""}>
                      
                    {image ? (
                      <button onClick={()=>{
                        displaySwitcher === "text" ? setDisplaySwitcher("image") : setDisplaySwitcher("text") 
                      }}>
                        Display {displaySwitcher === "text" ? "image" : "text"}
                      </button>
                    ) : null}
                    
                    {image ? ( 
                      <button onClick={deleteImage}>Delete</button> 
                    ) : null}

                  </div>
                  <div>
                    <div>
                      {tempImage === null ? "No image seleted" : tempImage.name}
                    </div>
                    <div>
                      <input type="file" onChange={changeImage} ref={candidateInputEdit}/>
                      
                      {tempImage ? (
                        <button onClick={()=>{
                          candidateInputEdit.current.value = null;
                          setTempImage(null)
                        }}>Cancel</button>
                      ) : null}
                      {tempImage?(
                        <button onClick={uploadImage}>Upload</button>
                      ) : null}

                    </div>
                  </div>
                </div>
                <div className="right">
                  <button value="left" className={position==="left"?"active":""} onClick={e=>{setPosition(e.target.value)}}>Left</button>
                  <button value="center" className={position==="center"?"active":""} onClick={e=>{setPosition(e.target.value)}}>Center</button>
                  <button value="right" className={position==="right"?"active":""} onClick={e=>{setPosition(e.target.value)}}>Right</button>
                </div>
              </div>
              <div>

                {displaySwitcher === "text" ? (
                  <textarea value={text} className="text" onChange={e => {setText(e.target.value)}}/>
                ) : (
                  <img src={image} className="image"/>
                )}

              </div>
            </div>
            <div className="footer">
              <button onClick={updateArticle}>Edit</button>
              <button onClick={deleteArticle}>Delete</button>
            </div>
          </section>

        ):(

          <section className="individual-article new-article">
            <div className="header">
              <button onClick={() => setSectionState("show")}>X</button>
            </div>
            <div className="content">
              <div className={"title"}>
                <div className="left">Title</div> 
                <textarea placeholder="Title goes here..." className="right" onChange={e => {setTitle(e.target.value)}}/>
              </div>
              <div className="picture">
                <div className="left">
                  <div>
                    {tempImage === null ? "No image seleted" : tempImage.name}
                  </div>    
                  <input type="file" ref={candidateInputNew} onChange={changeImage}/>

                  {tempImage ? (
                    <button onClick={()=>{
                      candidateInputNew.current.value = null;
                      setTempImage(null)
                    }}>Cancel</button>
                  ) : null}
                    
                </div>
                <div className="right">
                  <button value="left" className={position==="left"?"active":""} onClick={e=>{setPosition(e.target.value)}}>Left</button>
                  <button value="center" className={position==="center"?"active":""} onClick={e=>{setPosition(e.target.value)}}>Center</button>
                  <button value="right" className={position==="right"?"active":""} onClick={e=>{setPosition(e.target.value)}}>Right</button>
                </div>
              </div>
              <div>
                <textarea placeholder="Text goes here..." className="text" onChange={e => {setText(e.target.value)}}/>
              </div>
            </div>
            <div className="footer">
              <button onClick={createArticle}>Create and upload image</button>
            </div>
          </section>

        ))} 

      </div>
    )
  }else{
    return(
      <div className={"message404"}>you must be logged :(</div>
    )
  }
}
  
export default Dashboard;
  